<template>
  <div class="page"
    v-nav="{
      title: '注册赚钱',
      showBackButton: true,
      onBackButtonClick: back,
      showMenuButton: true,
      menuButtonText: '任务进度',
      onMenuButtonClick: progress
    }">
    <div v-if="spot" class="spot"></div>

    <div class="page-content">

      <scroller style="top: 44px; z-index: 0;">
        <div class="empty-list" v-if="empty">
          <img src="//qianka.b0.upaiyun.com/images/bb90eb799d65bf89b626cb12026b65af.png" alt="No Data">
          <span>一大波任务即将上线</span><br/>
          <span>请保持关注哦</span>
        </div>

        <!-- 进行中 -->
        <gaoe-card
          :tasks="ongoing"
          :on-task-click="goGaoeDetail"
        >
        </gaoe-card>

        <!-- 投放中 -->
        <gaoe-card
          title="投放中"
          :tasks="tasks"
          :on-task-click="goGaoeDetail"
        >
        </gaoe-card>

        <div style="height: 100px;"></div>
      </scroller>
    </div>

    <AssistantInvalid mode="manual" />
    <AssistantUnopen mode="manual" />
  </div>
</template>
<style lang="scss" scoped>
  .page-content {
    padding-top: 44px;
  }

  .empty-list {
    position: relative;
    top: 181px;
    color: #C5C4C3;
    font-size: 16px;
    text-align: center;

    img {
      display: block;
      width: 70px;
      height: 52px;
      position: relative;
      left: 50%;
      margin-left: -35px;
      margin-bottom: 15px;
    }
  }

  .spot {
    position: fixed;
    display: inline-block;
    border-radius: 50%;
    top: 13px;
    right: 11px;
    width: 4px;
    height: 4px;
    background-color: #EA5A49;
    z-index: 100;
  }
</style>
<script>
  import {mapGetters, mapActions} from 'vuex'
  import AssistantInvalid from '@/task/popups/AssistantInvalid.vue'
  import AssistantUnopen from '@/task/popups/AssistantUnopen.vue'
  import GaoeCard from './GaoeCard'

  import {
    KEY_ASSISTANT_GUIDE,
    KEY_LAST_USER_CONNECT_KEY,
    COOKIE_KEY_LAST_UID,
    KEY_GAOE_TASK_READ,
    KEY_GAOE_TASK_COMPARE_FAIL,
    KEY_GAOE_TASK_PROCESS
  } from 'constants'

  import {
    popup_must_know,
    popup_compare_fail,
    popup_pleaseant_remind,
    popup_snatch_new_task,
    popup_user_freeze
  } from './popups.js'

  import {
    popup_back_to_desktop
  } from '../task_deprecated/popups.js'

  import downloadAssistantPopupMixin from '@/task/views/downloadAssistantPopupMixin'

  export default {
    components: {
      GaoeCard,
      AssistantInvalid,
      AssistantUnopen
    },

    mixins: [downloadAssistantPopupMixin],

    data () {
      return {
        // 助手信息
        assistant: {
          app_name: '',
          app_icon: '',
          download_url: ''
        },
        ongoing: [],
        tasks: [],
        spot: 0,
        empty: 0,
        isvalid: 1 // 账户是否冻结，用于在抢任务前弹层
      }
    },

    computed: {
      ...mapGetters('assistant', [
        'isNewbie'
      ])
    },

    mounted () {
      this.fetchData()
    },

    watch: {
      '$route': 'fetchData'
    },

    methods: {
      ...mapActions('assistant', {
        'isAssistantAlive': 'alive',
        'syncAssistantUnopen': 'syncAssistantUnopen',
        'syncAssistantInvalid': 'syncAssistantInvalid'
      }),

      fetchData () {
        this.isAssistantAlive()
          .then(() => {
            $storage.set(KEY_LAST_USER_CONNECT_KEY, $cookie.get(COOKIE_KEY_LAST_UID))
            this.refresh('alive')
          }, () => {
            $api.assistantInfo()
              .then(res => res.data.payload)
              .then((payload) => {
                // 保存任务助手教程初始化信息
                let guide = $storage.get(KEY_ASSISTANT_GUIDE)
                // 本地不存在助手信息或助手应用发生了变化，则重置助手教程信息
                if (
                  !guide ||
                    (guide && guide.app_name !== payload.app_name)
                ) {
                  $storage.set(KEY_ASSISTANT_GUIDE, {
                    showed: 0,
                    app_type: payload.app_type,
                    app_name: payload.app_name,
                    app_icon: payload.app_icon
                  })
                }

                return {
                  assistant: payload
                }
              })
              .then(payload => {
                this.assistant = payload.assistant

                // 显示任务助手教程
                let guide = $storage.get(KEY_ASSISTANT_GUIDE)
                if (guide && guide.showed === 0 && guide.download_click === 1) {
                  let popup

                  if (guide.app_type === 1) { // 线上版
                    popup = $popup.fromTemplate(
                      popup_back_to_desktop.template(guide.app_icon),
                      popup_back_to_desktop.options
                    )

                    popup.show().then((buttonIndex) => {
                      if (buttonIndex === 0) {
                        guide.showed = 1
                        $storage.set(KEY_ASSISTANT_GUIDE, guide)
                      }
                    })
                  }
                }
              })

            this.refresh()
          })
      },

      refresh (status) {
        let read = $storage.get(KEY_GAOE_TASK_READ) || []
        let isCompare = 0
        let gaoeListApi

        // 根据钥匙是否开启走不同接口
        if (status === 'alive') {
          gaoeListApi = $api.aliveGaoeList()
        } else {
          gaoeListApi = $api.GaoeList()
        }
        gaoeListApi
          .then(res => res.data && res.data.payload)
          .then((payload) => {
            // 小红点是否显示的比对逻辑
            this.spot = 0
            if (payload.progress) {
              let process = $storage.get(KEY_GAOE_TASK_PROCESS) || []
              let apiProcess = payload.progress
              _.each(apiProcess, (progress) => {
                let taskProcess = progress.id + ':' + progress.status
                if (process.indexOf(taskProcess) <= -1) {
                  this.spot = 1
                }
              })
            }

            // 用户是否冻结字段
            this.isvalid = payload.isvalid

            if (payload.ongoing_task) {
              let ongoing = payload.ongoing_task
              _.each(ongoing, (task) => {
                task.show = 1
              })
              this.ongoing = ongoing
            }
            if (payload.tasks) {
              let tasks = payload.tasks
              if (payload.appids) {
                let appids = payload.appids
                let diffAppids = _.difference(appids, read)
                _.each(tasks, (task) => {
                  let failGaoeAppids = $storage.get(KEY_GAOE_TASK_COMPARE_FAIL) || []
                  if (failGaoeAppids.indexOf(task.appid) > -1) {
                    task.show = 0
                  } else {
                    task.show = 1
                  }
                  if (task.is_idfa_task !== 0) {
                    isCompare = 1
                  }
                  if (read.length !== 0) {
                    if (diffAppids.indexOf(task.appid) > -1) {
                      task.is_new = 1
                    } else {
                      task.is_new = 0
                    }
                  }
                })
                this.tasks = tasks

                let newAppids = _.union(read, diffAppids)
                $storage.set(KEY_GAOE_TASK_READ, newAppids)

                if (isCompare === 1) {
                  $assistant.updateState()
                }
              }
            }
            if ((this.tasks.length === 0) && (this.ongoing.length === 0)) {
              this.empty = 1
            }
          })
      },

      goGaoeDetail (task) {
        // 点击了进行中的任务，尝试直接进入任务详情页面
        if (task.status === 2) {
          let route = {
            name: 'gaoe_detail',
            params: {
              id: task.id
            }
          }
          $router.forward(route)
          return
        }

        if (task.is_idfa_task === 1) {
          $assistant.alive()
            .then(() => {
              this.realStart(task)
            }, () => { // 未开启钥匙
              this.showOpenOrDownloadAssitant()
            })
        } else {
          this.realStart(task)
        }
      },

      realStart (task) {
        let ongoing = this.ongoing
        // 用户如果冻结，优先弹冻结提示弹层
        if (this.isvalid === 0) {
          let popup = $popup.fromTemplate(
            popup_user_freeze.template(),
            popup_user_freeze.options
          )
          popup.show()
          return
        }
        // 不能同时抢两个任务弹层
        if (ongoing.length !== 0) {
          let p = popup_snatch_new_task
          let popup = $popup.fromTemplate(
            p.template,
            p.options
          )
          popup.show().then((buttonIndex) => {
            if (buttonIndex === 1) {
              if (task.relatives) {
                this.pleasantRemind(task)
              } else {
                this.mustKnow(task)
              }
            }
          })
        } else {
          if (task.relatives) {
            // 温馨提示弹层
            this.pleasantRemind(task)
          } else {
            // 任务须知弹层
            this.mustKnow(task)
          }
        }
      },

      pleasantRemind (task) {
        let p = popup_pleaseant_remind
        let popup = $popup.fromTemplate(
          p.template,
          p.options
        )
        popup.show().then((buttonIndex) => {
          if (buttonIndex === 1) {
            this.snatchTask(task)
          }
        })
      },

      mustKnow (task) {
        let listPopup = task.list_popup
        if (listPopup.text) {
          let p = popup_must_know
          let popup = $popup.fromTemplate(p.template(task.list_popup.text), p.options)
          let sec = 3
          popup.updateButtonTextByIndex(1, '继续（3s）')

          let countDownTimer = setInterval(() => {
            if (sec > 1) {
              popup.updateButtonTextByIndex(1, `继续（${--sec}s）`)
            } else {
              clearInterval(countDownTimer)
              popup.updateButtonTextByIndex(1, '继续')
              popup.updateButtonStatusByIndex(1, true)
            }
          }, 1000)

          popup.show().then((buttonIndex) => {
            clearInterval(countDownTimer)

            if (buttonIndex === 1) {
              this.snatchTask(task)
            }
          })

          setTimeout(() => { popup.updateButtonStatusByIndex(1, false) }, 0)
        } else {
          this.snatchTask(task)
        }
      },

      snatchTask (task) {
        // 点击非比对任务
        if (task.is_idfa_task === 0) {
          $api.gaoeStart(task.id)
            .then(res => res.data && res.data.payload)
            .then(payload => {
              let starting = false
              if (!starting) {
                $loading.show('拼命争抢任务中...')
                starting = true
              }
              if (payload.status === 2) {
                setTimeout(() => {
                  $toast.show('抢到任务，正在跳转...', 1000)

                  setTimeout(() => {
                    $router.forward({
                      name: 'gaoe_detail',
                      params: {
                        id: task.id
                      }
                    })
                  }, 1000)
                }, 500)
              } else {
                setTimeout(() => {
                  $toast.show('抢任务失败', 1000)
                }, 2000)
              }
            })
        } else {
          $assistant.alive()
            .then(() => {
              let starting = false
              if (!starting) {
                $loading.show('任务匹配中，请稍后')
                starting = true
              }
              return $api
                .compareGaoeStart(task.id)
                .then(res => res.data)
            }, () => { // 未开启钥匙
              this.showOpenOrDownloadAssitant()
            })
            .then(data => {
              if (data.err_code === 10000) {
                $assistant.updateState()
                $loading.show('任务匹配中，请稍后')
                setTimeout(() => {
                  $loading.hide()
                }, 2000)
                return
              } else if (data.err_code === 10001) {
                $loading.hide()
                this.compareFail(task)
                let p = popup_compare_fail
                let popup = $popup.fromTemplate(p.template, p.options)
                popup.show().then(buttonIndex => {
                  if (buttonIndex === 0) {
                    task.show = 0
                    return
                  }
                })
              }
              return data.payload
            })
            .then(payload => {
              if (payload.status === 2) {
                setTimeout(() => {
                  $toast.show('抢到任务，正在跳转...', 1000)

                  $api.highearnSendIDFA(task.id)
                  .then(res => res.data)

                  setTimeout(() => {
                    $router.forward({
                      name: 'gaoe_detail',
                      params: {
                        id: task.id
                      }
                    })
                  }, 1000)
                }, 500)
              }
            })
        }
      },

      compareFail (task) {
        let failGaoeAppids = $storage.get(KEY_GAOE_TASK_COMPARE_FAIL) || []
        failGaoeAppids.push(task.appid)
        $storage.set(KEY_GAOE_TASK_COMPARE_FAIL, failGaoeAppids)
      },

      back () {
        let path = _.get(this.$route.query, 'backroute', '/dashboard')
        $router.back({ path })
      },

      progress () {
        $router.forward('/gaoe/progress')
      },

      showOpenOrDownloadAssitant () {
        if (this.isNewbie) { // 如果是新用户的话
          this.showDownloadAssistantPopup()
          return
        }

        this.syncAssistantUnopen()
        this.syncAssistantInvalid()
      }
    }
  }
</script>



