<!--高额任务卡片-->
<template>
  <div class="gaoe-card" v-if="tasks.length > 0 || subTitle.length > 0">
    <div class="divider"></div>

     <list class="list list-ios task-card">
      <item v-if="title" class="task-item-header hl-item-borderless" thin>
        <span class="title" v-text="title"></span>
        <span class="sub-title" v-html="subTitle"></span>
      </item>

      <item class="task-item" v-for="(t, index) in tasks" :key="index" v-if="t.show" @click.native="taskClicked(t)">
        <img class="icon" :src="t.icon" alt="">
        <div class="information">
          <div class="title" v-text="t.title"></div>
          <div v-if="t.is_new" class="new"><img src="//qianka.b0.upaiyun.com/images/261ba2b28fd81ab2b41c33144b990aac.png" alt=""></div>
          <div class="status" v-if="t.compare_status && t.compare_status == 1||3" v-text="compareText[t.compare_status]"></div>
        </div>

        <scalable class="tags" width="200" height="18">
          <span v-for="tag in t.tags" class="tag-box">
            <span v-if="typeof tag == 'string'" v-text="tag" class="tag"></span>
            <span v-if="typeof tag == 'object' && tag.text" v-text="tag.text" class="tag"
              :class="{'assertive': tag.theme == 'assertive'}"></span>
          </span>
        </scalable>

        <!-- 任务进行中 -->
        <scalable v-if="t.status == 2" class="reward" width="80" height="40">
          <span class="positive" style="font-size: 28px; line-height: 80px;">进行中</span>
        </scalable>

        <scalable v-if="t.status == 1 && t.reward" class="reward" width="80" height="40">
          <span class="yuan">元</span>
          <span class="amount">{{ (t.reward)}}</span>
          <span class="plus">+</span>
        </scalable>
      </item>
    </list>

     <item class="task-card-footer-text"
      v-if="footerText">
      <span v-text="footerText"></span>
    </item>

    <item class="task-card-footer-button"
      v-if="footerButtonText"
      @click.native="footerButtonClicked()">
      <span v-text="footerButtonText"></span>
    </item>
  </div>
</template>
<style lang="scss">
  @import '../../scss/variables';
  @import "../../scss/mixins";

  .gaoe-card {
    .task-card {
      &::before {
        border-top: none;
      }

      &::after {
        border-bottom: none;
      }

      margin-bottom: 0px;

      /* reset border and color */
      .task-item,
      .task-item-header,
      .task-item-footer {
        &:first-of-type::before {
          border-top: none;
        }

        &:last-of-type::after {
          border-bottom: none;
        }

        color: #484746;
      }

      .task-item-header {
        .sub-title {
          margin-left: 20px;
          font-size: 12px;
          color: #b5b5b5;
        }
      }

      .task-item {
        height: 80px;
        padding: 16px 16px 16px 48px + 10px;

        .icon {
          width: 48px;
          height: 48px;
          position: absolute;
          top: 16px;
          left: 0px;
          border-radius: 4px;
        }

        .information {
          position: relative;
          height: 31px;

          .title {
            float: left;
            font-size: 16px;
            color: #484746;
            line-height: 22px;
            margin-bottom: 9px;
          }

          .new {
            float: left;
            width: 31px;
            height: 27px;
            padding-left: 4px;

            img {
              float: left;
              top: -2px;
              position: absolute;
              width: 27px;
              height: 17px;
            }
          }

          .status {
            float: left;
            padding-top: 6px;
            text-align: center;
            width: 66px;
            height: 31px;
            font-size: 11px;
            color: #B5B5B5;
          }
        }

        .tags {
          .tag {
            height: 36px;
            border-radius: 4px;
            font-size: 22px;
            line-height: 32px;
            padding: 2px 10px;
            margin-right: 8px;
            color: #b5b5b5;
            border: 1px solid #c5c4c3;

            &.assertive {
              color: $assertive;
              border: 1px solid $assertive;
            }
          }
        }

        .reward {
          position: absolute;
          top: 20px;
          right: 16px;
          color: $assertive;

          span {
            display: block;
            float: right;
          }

          .plus, .yuan {
            font-size: 22px;
            line-height: 22px;

            @include font-regular();
          }

          .plus {
            margin-top: 36px;
          }

          .yuan {
            margin-top: 36px;
          }

          .amount {
            font-size: 48px;
            line-height: 80px;
            margin: 0 4px;

            @include font-regular();
          }
        }

        .reward.muted {
          color: #b5b5b5;
        }
      }
    }

    .task-card-footer-text,
    .task-card-footer-button-text {
      &::after {
        border-bottom: none;
      }

      line-height: 12px;
      font-size: 12px;
      text-align: center;
    }

    .task-card-footer-text {
      color: #b5b5b5;
      height: 30px;
      min-height: 30px;
      padding: 9px 0;
    }

    .task-card-footer-button-text {
      color: $assertive;
      height: 38px;
      min-height: 38px;
      padding: 13px 0;
    }
  }
</style>
<script>
  export default {
    props: {
      title: {
        type: String,
        default: ''
      },
      subTitle: {
        type: String,
        default: ''
      },
      tasks: {
        type: Array,
        default: []
      },
      onTaskClick: Function,
      footerText: {
        type: String,
        default: ''
      },
      footerButtonText: {
        type: String,
        default: ''
      },
      onFooterButtonClick: {
        type: Function
      },
      card: Object,
      compareStatus: {
        type: Number,
        default: 0
      }
    },

    data () {
      return {
        compareText: {
          '1': '（匹配中…）',
          '3': '（匹配失败）'
        }
      }
    },

    methods: {
      taskClicked (task) {
        if (this.onTaskClick) this.onTaskClick(task)
      },

      footerButtonClicked (task) {
        if (this.onFooterButtonClick) this.onFooterButtonClick()
      }
    }
  }
</script>



